<template>
    <div class="home">
        <div class="djtIndex-top">
            <img src="../../assets/images/index-back.png" alt="" class="djtIndex-top-back">
            <div class="djtIndex-top-mean">
                <div class="djtIndex-top-mean-left">
                    <img src="../../assets/images/logo.png" alt="" class="djtIndex-top-mean-left-img">
                    <span class="djtIndex-top-mean-left-text">Donald John Trump</span>
                </div>
                <div class="djtIndex-top-mean-right">
                    <template v-if="!address">
                        <span class="djtIndex-top-mean-right-text" @click="showMask()">{{ short_address }}</span>
                    </template>
                    <template v-if="address">
                        <span class="djtIndex-top-mean-right-text">{{ short_address }}</span>
                    </template>

                    <img src="../../assets/images/index-lang.png" alt="" class="djtIndex-top-mean-right-img1" @click="showLang()">
                    <img src="../../assets/images/index-mean.png" alt="" class="djtIndex-top-mean-right-img2" @click="showMean">
                </div>
            </div>
        </div>
        <div class="djtIndex-content">
            <div class="djtIndex-content-title">{{ lang_list.index_title }}</div>
            <div class="djtIndex-content-desc">
                {{ lang_list.index_desc1 }}
                <br /><br />
                {{ lang_list.index_desc2 }}
            </div>
            <div class="djtIndex-content-tab">
                <div class="djtIndex-content-tab-button-active"></div>
                <div class="djtIndex-content-tab-button"></div>
            </div>
            <div>
                <img src="../../assets/images/index-tab1.png" alt="" class="djtIndex-content-tab-img">
            </div>
            <div class="djtIndex-content-title"> {{ lang_list.index_title1 }}</div>
            <div class="djtIndex-content-desc">
                {{ lang_list.index_desc3 }}
            </div>
            <div>
                <div class="djtIndex-content-title djtIndex-content-margin"> {{ lang_list.index_title3 }}</div>
                <div class="djtIndex-content-desc djtIndex-content-text">
                    <div>• {{ lang_list.index_item1 }}</div>
                    <div>• {{ lang_list.index_item2 }}</div>
                    <div>• {{ lang_list.index_item3 }}</div>
                </div>
            </div>
            <div>
                <img src="../../assets/images/index-tab2.png" alt="" class="djtIndex-content-tab-img">
            </div>
            <div class="djtIndex-content-title">{{ lang_list.index_title4 }}</div>
            <div class="djtIndex-content-bottom">
                <div style="position: relative;">
                    <img src="../../assets/images/index-tab3.png" alt="" class="djtIndex-content-bottom-img">
                    <div class="djtIndex-text">
                        {{ lang_list.index_fen1 }} <br/> {{ lang_list.index_fen2 }}
                    </div>
                </div>
                <div class="djtIndex-content-bottom-tab">
                    <div class="djtIndex-content-bottom-tab-left">
                        <div class="djtIndex-content-bottom-tab-left-item">
                            <div class="djtIndex-content-bottom-tab-left-text1"></div>
                            <span>{{ lang_list.index_fen3 }}</span>
                        </div>
                        <div class="djtIndex-content-bottom-tab-left-item">
                            <div class="djtIndex-content-bottom-tab-left-text2"></div>
                            <span>{{ lang_list.index_fen4 }}</span>
                        </div>
                        <div class="djtIndex-content-bottom-tab-left-item">
                            <div class="djtIndex-content-bottom-tab-left-text3"></div>
                            <span>{{ lang_list.index_fen5 }}</span>
                        </div>
                        <div class="djtIndex-content-bottom-tab-left-item">
                            <div class="djtIndex-content-bottom-tab-left-text4"></div>
                            <span>{{ lang_list.index_fen6 }}</span>
                        </div>
                        <div class="djtIndex-content-bottom-tab-left-item">
                            <div class="djtIndex-content-bottom-tab-left-text5"></div>
                            <span>{{ lang_list.index_fen7 }}</span>
                        </div>
                        <div class="djtIndex-content-bottom-tab-left-item">
                            <div class="djtIndex-content-bottom-tab-left-text6"></div>
                            <span>{{ lang_list.index_fen8 }}</span>
                        </div>
                    </div>
                    <div>
                        <div class="djtIndex-content-bottom-tab-left-item">
                            33333{{ lang_list.index_fen9 }}
                        </div>
                        <div class="djtIndex-content-bottom-tab-left-item">
                            30%
                        </div>
                        <div class="djtIndex-content-bottom-tab-left-item">
                            8%
                        </div>
                        <div class="djtIndex-content-bottom-tab-left-item">
                            7%
                        </div>
                        <div class="djtIndex-content-bottom-tab-left-item">
                            15%
                        </div>
                        <div class="djtIndex-content-bottom-tab-left-item">
                            70%
                        </div>
                    </div>
                </div>
            </div>
            <div class="djtIndex-content-contract" @click="toContract()">
                {{ lang_list.index_contract }}:0x41D8675E6C64939c3245908EEaf827A995Fc47Ff
            </div>
            <div class="djtIndex-content-button">
                <div class="djtIndex-content-button1" @click="toBuyDjt()">{{ lang_list.index_buy1 }}</div>
                <div class="djtIndex-content-button2" @click="toKuang()">{{ lang_list.index_buy2 }}</div>
            </div>
        </div>
        <div v-if="meanShow" class="index_pop_content">
            <div class="index_pop_top">
                <div>
                    <img src="../../assets/images/logo.png" alt="" class="index_pop_top-left">
                </div>
                <div @click="closeMean">
                    <img src="../../assets/images/close.png" alt="" class="index_pop_top-right">
                </div>
            </div>
            <div class="index_pop_main_tab">
                <div class="index_pop_middle"  @click="toIndex()">
                    <div class="index_pop_middle_left">
                        <img src="../../assets/images/index-home.png" alt="">
                        <span>{{ lang_list.index_mean1 }}</span>
                    </div>
                </div>
                <div class="index_pop_middle" @click="toBuyDjt()">
                    <div class="index_pop_middle_left">
                        <img src="../../assets/images/duihuan.png" alt="">
                        <span>{{ lang_list.index_mean2 }}</span>
                    </div>
                </div>
                <div class="index_pop_middle" @click="toKuang()">
                    <div class="index_pop_middle_left">
                        <img src="../../assets/images/index-kuang.png" alt="">
                        <span>{{ lang_list.index_mean3 }}</span>
                    </div>
                </div>
                <div class="index_pop_middle" @click="toOrder">
                    <div class="index_pop_middle_left">
                        <img src="../../assets/images/index-record.png" alt="">
                        <span>{{ lang_list.index_mean4 }}</span>
                    </div>
                </div>
                <div class="index_pop_middle" @click="toWhite">
                    <div class="index_pop_middle_left">
                        <img src="../../assets/images/index-white.png" alt="">
                        <span>{{ lang_list.index_mean5 }}</span>
                    </div>
                </div>
                <div class="index_pop_bottom">
                    <div class="index_pop_bottom-left" @click="toTwitte()"><img src="../../assets/images/Twitte.png" alt=""></div>
                    <div class="index_pop_bottom-left" @click="todian()"><img src="../../assets/images/dianbao.png" alt=""></div>
                </div>
            </div>
        </div>
        <div v-if="maskShow" class="index_mask">
            <div class="index_mask-content">
                <div class="index_mask-title">{{ lang_list.index_mask }}</div>
                <img src="../../assets/images/matemask.png" alt="" class="index_mask-img">
            </div>
            <div @click="closeMask()">
                <img src="../../assets/images/closed.png" alt="" class="index_mask-close">
            </div>
        </div>
        <div v-if="langShow" class="index_lang">
            <div :class="lang=='zh'?'index_lang-button-active':'index_lang-button'" @click="switchLang('zh')">中文</div>
            <div :class="lang=='en'?'index_lang-button-active':'index_lang-button'" @click="switchLang('en')">EN</div>
        </div>
    </div>
</template>
<script>
    import {WOW} from 'wowjs';
    let provider, signer, defaultAccount, networkVersion;
    let baseUrl = "";
    export default {
        data() {
            return {
                lang:'zh',
                lang_list:{},
                langShow:false,
                address: '',
                short_address: 'connect',
                parents:'',
                meanShow: false,
                maskShow:false,
            }
        },
        created: function () {

        },
        async mounted() {
            this.lang = sessionStorage.getItem('lang')?sessionStorage.getItem('lang'):'en';
            this.lang_list = this.en;
            switch(this.lang){
                case 'zh':
                    this.lang_list = this.zh;
                    break;
                case 'en':
                    this.lang_list = this.en;
                    break;
            }
            this.checkNet();
            var wow = new WOW(
                {
                    boxClass: 'wow',      // animated element css class (default is wow)
                    animateClass: 'animated', // animation css class (default is animated)
                    offset: 0,          // distance to the element when triggering the animation (default is 0)
                    mobile: true,       // trigger animations on mobile devices (default is true)
                    live: true,       // act on asynchronously loaded content (default is true)
                    callback: function (box) {
                        // the callback is fired every time an animation is started
                        // the argument that is passed in is the DOM node being animated
                    },
                    scrollContainer: null // optional scroll container selector, otherwise use window
                }
            );
            wow.init();
        },
        methods: {
            todian(){
                window.open('https://t.me/DJT_Group','_self');
            },
            toTwitte(){
                window.open('https://twitter.com/DJTsystem','_self');
            },
            toBuyDjt(){
                window.open('https://pancakeswap.finance/swap?outputCurrency=0x41D8675E6C64939c3245908EEaf827A995Fc47Ff&inputCurrency=0x55d398326f99059ff775485246999027b3197955&utm_source=tokenpocket','_self');
            },
            toContract(){
                window.open('https://bscscan.com/address/'+this.api.djtAddress,'_self');
            },
            toWhite(){
              window.open('/static/djt.pdf');
            },
            toIndex(){
                this.$router.push({
                    name: 'home',
                    params: {

                    }
                })
                this.meanShow = false;
            },
            toOrder(){
                this.$router.push({
                    name: 'order',
                    params: {

                    }
                })
                this.meanShow = false;
            },
            toKuang(){
                this.$router.push({
                    name: 'kuang',
                    params: {

                    }
                })
                this.meanShow = false;
            },
            switchLang(type){
                switch(type){
                    case 'zh':
                        sessionStorage.setItem('lang','zh');
                        break;
                    case 'en':
                        sessionStorage.setItem('lang','en');
                        break;
                }
                location.reload();
            },
            //显示菜单
            showLang(){
                if(this.langShow){
                    this.langShow = false;
                }else{
                    this.langShow = true;
                }

            },
            //显示菜单
            showMask(){
                this.maskShow = true;
            },
            //关闭菜单
            closeMask(){
                this.maskShow = false;
            },
            //显示菜单
            showMean(){
                this.meanShow = true;
            },
            //关闭菜单
            closeMean(){
                this.meanShow = false;
            },
            connectWallet() {
                if (this.address) {
                    this.short_address = this.address.substr(0, 3) + '...' + this.address.substr(this.address.length - 3)
                } else {
                    this.checkNet()
                }
            },
            getUrlParam(name) {
                var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)"); //构造一个含有目标参数的正则表达式对象
                var r = window.location.search.substr(1).match(reg);  //匹配目标参数
                if (r != null) return unescape(r[2]);
                return null; //返回参数值
            },
            async checkNet() {
                let _this = this;

                try {
                    provider = new ethers.providers.Web3Provider(web3.currentProvider);
                    signer = provider.getSigner();
                    _this.checkLogin();
                } catch (e) {
                    _this.$toast(_this.lang_list.index_warn);
                }

            },
            async checkLogin() {
                let _this = this;
                setInterval(function () {
                    window.ethereum.enable()
                        .then(function (accounts) {
                            if (accounts.length == 0 && defaultAccount != "") {
                                window.location.reload();
                                return;
                            }
                            if (accounts.length == 0) {
                                window.location.reload();
                                return;
                            }
                            if (accounts[0] != defaultAccount) {
                                defaultAccount = accounts[0];
                                window.location.reload();
                                console.log("account change, start bat updateData!")
                            }
                        })
                        .catch(function (reason) {
                            // Handle error. Likely the user rejected the login:
                            console.log(reason === "User rejected provider access")
                        })
                }, 1000);
                window.ethereum.enable()
                    .then(async function (accounts) {
                        if (accounts.length == 0) {
                            console.log('33333');
                            return;
                        }
                        defaultAccount = accounts[0];
                        console.log("defaultAccount: ", defaultAccount);
                        networkVersion = window.ethereum.networkVersion
                        if (networkVersion == 1) {  //以太坊主网
                            _this.type = 'ETH';
                            _this.$toast(_this.lang_list.index_warn);
                            return false;
                        }
                        else if (networkVersion == 42) {   //以太坊测试网  K
                            _this.type = 'ETH';
                            _this.$toast(_this.lang_list.index_warn);
                            return false;
                        }
                        else if (networkVersion == 128) {  //HECO主网
                            _this.type = 'HT';
                            _this.$toast(_this.lang_list.index_warn);
                            return false;
                        }
                        else if (networkVersion == 256) { //HECO测试网
                            _this.type = 'HT';
                            _this.$toast(_this.lang_list.index_warn);
                            return false;
                        }
                        else if (networkVersion == 56) { //币安主网
                            _this.type = 'BNB';
                        }
                        else {
                            _this.$toast(_this.lang_list.index_warn);
                            return false;
                        }
                        if (defaultAccount != '') {
                            _this.address = defaultAccount;
                        }
                    });
                setTimeout(function () {
                    if (defaultAccount) {
                        _this.address = defaultAccount;
                        _this.short_address = _this.address.substr(0, 3) + '...' + _this.address.substr(_this.address.length - 3)
                    }

                }, 500)
            },
        }
    }
</script>


